body {
  font-family: 'Times New Roman', Times, serif;
  margin: 20px;
}
ol, ul {
  padding-left: 10px;
}
.board-row:after {
  clear: both;
  content: "";
  display: table;
}
.status {
  margin-bottom: 10px;
}
.square {
  background: rgb(0, 0, 0);
  border: 1px solid #999;
  float: right;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 34px;
  margin-right: 1px;
  margin-top: 1px;
  padding: 0;
  text-align: center;
  width: 34px;
  flex-wrap: wrap;

}
.square:focus {
  outline: none;
}
.kbd-navigation .square:focus {
  background: #ddd;
}
.game {
  display: flex;
  flex-direction: row;
}
.game-info {
  margin-left: 340px;
}